import React, { useContext } from 'react';
import AppContext from './AppContext';
import { beautifyPokemonName, TYPES_TO_COLORS } from './helper';
import PokedexSprite from './PokedexClickedPokemonSprite';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from './components/Card';

function PokedexNameAndType() {
  const {
    dex,
    clickedSpeciesId,
  } = useContext(AppContext);
  if (clickedSpeciesId == null) {
    return (
      <div>
        No data.
      </div>
    );
  }

  const { name, types } = dex[clickedSpeciesId];
  const [type1, type2] = types;
  const type1P = <p style={{ color: `rgb(${TYPES_TO_COLORS[type1].join(',')})` }}>{type1}</p>;
  const type2P = type2 === undefined
    ? null
    : <p style={{ color: `rgb(${TYPES_TO_COLORS[type2].join(',')})` }}>{type2}</p>;
  return (
    <Card>
      <CardHeader>
        <CardTitle>{beautifyPokemonName(name)}</CardTitle>
        <CardDescription>
          <span className="inline-flex font-bold">
            {type1P}
            &nbsp;
            {type2P}
          </span>
        </CardDescription>
        <CardContent>
          <div className="block my-1 flex justify-center">
            <PokedexSprite />
          </div>
        </CardContent>
      </CardHeader>
    </Card>
  );
}

export default PokedexNameAndType;
